import React from 'react'
import "./Footer.css";
import Correo from './images/Correo.png'
import Whatsapp from './images/Whatsapp.svg'
import Mapas from './images/Mapas.svg'

const Footer = () => {
    return(
        <div className="footerContainer">
            <div className="FooterColumn">1</div>
            <div className="FooterColumn">
                <div>
                    <ul className="block">
                        <li className="footer-logo">
                            <img src={Whatsapp} alt="telefono" height="30" />
                        </li>
                        <li className="footer-data">954 853 723</li>
                    </ul>
                    <ul className="block">
                        <li className="footer-logo">
                            <img src={Correo} alt="telefono" height="30" />
                        </li>
                        <li className="footer-data">BitStationBusiness@gmail.com</li>
                    </ul>
                    <ul className="block">
                        <li className="footer-logo">
                            <img src={Mapas} alt="telefono" height="30" />
                        </li>
                        <li className="footer-data">Av. Los Héroes 135 - San Juan de Miraflores, Lima-Perú</li>
                    </ul>
                </div>
            </div>
        </div>
    )
}

export default Footer;